import axios from "axios"
axios.defaults.withCredentials = true

// let BASE_URL = "http://localhost:8081/";
let BASE_URL = "http://18.119.99.53:8081/";

export default {
    async isLogin() {
        let res = await axios.get(BASE_URL + "is-login", {
        })
            .then(function (response) {
                return response;
            })
            .catch(function (error) {
                console.log(error);
            });
        return JSON.parse(res.data);
    },
    async login(username, password) {
        let res = await axios.post(BASE_URL + "login", {
            username: username,
            password: password
        })
            .then(function (response) {
                return response;
            })
            .catch(function (error) {
                console.log(error);
            });
        return JSON.parse(res.data);
    },
    async logout() {
        let res = await axios.post(BASE_URL + "logout", {})
            .then(function (response) {
                return response;
            })
            .catch(function (error) {
                console.log(error);
            });
        return JSON.parse(res.data);
    },
    async register(username, password) {
        let res = await axios.post(BASE_URL + "register", {
            username: username,
            password: password
        })
            .then(function (response) {
                return response;
            })
            .catch(function (error) {
                console.log(error);
            });
        return JSON.parse(res.data);
    },

}
