<template>
  <div class="lobby">
    <Nav ref="nav" @updateRoomListRequest="updateRoomListRequest"/>
    <RoomList ref="rl"/>
  </div>
</template>

<script>
import RoomList from "@/components/RoomList";
import RoomService from "@/services/RoomService";
import Nav from "@/components/partials/Nav";

export default {
  name: "Lobby",
  components: {RoomList, Nav},
  data() {
    return {
      isLogin: false,
    }
  },
  sockets: {},
  async created() {
    this.$socket.disconnect();
    await RoomService.exitRoom();
  },
  methods: {
    updateRoomListRequest: function () {
      this.$refs.rl.updateRoomList();
    },
  }
}
</script>

