import axios from "axios"
axios.defaults.withCredentials = true

// const BASE_URL = "http://localhost:8081/";
const BASE_URL = "http://18.119.99.53:8081/";
export default {
    async getRoomList() {
        let res = await axios.get(BASE_URL + "rooms");
        return JSON.parse(res.data);
    },
    async getPlayers(roomId) {
        let res = await axios.get(BASE_URL + "players/" + roomId);
        return JSON.parse(res.data);
    },
    async createRoom(roomName, roomPassword) {
        let res = await axios.post(BASE_URL + "create-room", {
            roomName: roomName,
            roomPassword: roomPassword
        });
        return JSON.parse(res.data);
    },
    async fetchRoomBasicInfo(roomId) {
        let res = await axios.get(BASE_URL + "room-basic-info/" + roomId);
        return JSON.parse(res.data);
    },
    async needPassword(roomId) {
        let res = await axios.get(BASE_URL + "room-need-password/" + roomId);
        return JSON.parse(res.data);
    },
    async enterRoom(roomId, roomName, roomPassword) {
        let res = await axios.post(BASE_URL + "enter-room", {
            roomId: roomId,
            roomName: roomName,
            roomPassword: roomPassword,
        });
        return JSON.parse(res.data);
    },
    async exitRoom() {
        let res = await axios.post(BASE_URL + "exit-room", {});
        return JSON.parse(res.data);
    },
    async getRoomSingle(roomId) {
        let res = await axios.get(BASE_URL + "room/" + roomId);
        return JSON.parse(res.data);
    },
    async getSeats(roomId) {
        let res = await axios.get(BASE_URL + "seats/" + roomId);
        return JSON.parse(res.data);
    },
    async changeSeat(targetSeatIndex) {
        let res = await axios.post(BASE_URL + "change-seat", {
            targetSeatIndex: targetSeatIndex,
        });
        return JSON.parse(res.data);
    },
    async getRoomOwner(roomId) {
        let res = await axios.get(BASE_URL + "owner/" + roomId);
        return JSON.parse(res.data);
    },
    async ready() {
        let res = await axios.post(BASE_URL + "ready", {});
        return JSON.parse(res.data);
    },
    async isReady() {
        let res = await axios.get(BASE_URL + "is-ready");
        return JSON.parse(res.data);
    },
    async play() {
        let res = await axios.post(BASE_URL + "play", {});
        return JSON.parse(res.data);
    },
    async bindRoom() {
        let res = await axios.post(BASE_URL + "bind-room", {});
        return JSON.parse(res.data);
    },
    async sendMessage(message) {
        let res = await axios.post(BASE_URL + "send-message", {
            message: message
        });
        return JSON.parse(res.data);
    },
    async getWord(roomId) {
        let res = await axios.get(BASE_URL + "draw-word/" + roomId);
        return JSON.parse(res.data);
    },
    async getGameState(roomId) {
        let res = await axios.get(BASE_URL + "game-state/" + roomId);
        return JSON.parse(res.data);
    },
    async getRoomMessages(roomId) {
        let res = await axios.get(BASE_URL + "room-messages/" + roomId);
        return JSON.parse(res.data);
    }
}
