<template>
  <div>
    <div v-if="seen">
      <div v-if="Number(room.start) === 1" class="card has-background-dark" @click.left="enterPreCheck($event)">
        <div class="content">
          <p class="content is-size-4 has-text-white">{{ room.roomName }}</p>
          <p class="content is-size-5 has-text-white"> {{ playerNum }} / 8
          </p>
        </div>
      </div>
      <div v-else-if="playerNum <= 3" class="card has-background-primary" @click.left="enterPreCheck($event)">
        <div class="content">
          <p class="content is-size-4 has-text-white">{{ room.roomName }}</p>
          <p class="content is-size-5 has-text-white"> {{ playerNum }} / 8
          </p>
        </div>
      </div>
      <div v-else-if="playerNum > 3 && playerNum <= 7" class="card has-background-warning" @click.left="enterPreCheck($event)">
        <div class="content">
          <p class="content is-size-4 has-text-white">{{ room.roomName }}</p>
          <p class="content is-size-5 has-text-white"> {{ playerNum }} / 8
          </p>
        </div>
      </div>
      <div v-else class="card has-background-danger">
        <div class="content">
          <p class="content is-size-4 has-text-white">{{ room.roomName }}</p>
          <p class="content is-size-5 has-text-white"> {{ playerNum }} / 8
          </p>
        </div>
      </div>
    </div>
    <div v-else>
    </div>
  </div>
</template>

<script>
import RoomService from "@/services/RoomService";

export default {
  name: "RoomCard",
  props: ['room'],
  created() {
    this.getPlayers(this.$route.params.id);
  },
  data() {
    return {
      playerNum: 0,
      inputRoomPassword: "",
      seen: false,
    };
  },
  methods: {
    getPlayers() {
      // NEW - Use the eventService to call the getEvents() method
      RoomService.getPlayers(this.room.roomId)
          .then(
              (result => {
                if (result.err === 0) {
                  let data = result.data;
                  if (Object.keys(data).length !== 0) {
                    this.seen = true;
                  }
                  this.$set(this, "playerNum", Object.keys(data).length);
                }
              }).bind(this)
          );
    },
    async enterPreCheck(e) {
      let roomInfo = await RoomService.fetchRoomBasicInfo(this.room.roomId);
      if (roomInfo.err === 0) {
        let data = roomInfo.data;
        if (Number(data.start) === 1) {
          alert("Game has started.");
          return;
        }
      } else {
        alert(roomInfo.errMsg);
        return;
      }
      this.$router.push({
        name: 'roomSingle',
        params: {
          id: this.room.roomId,
        },
      });
      e.preventDefault();
    }
  }
}
</script>

<style scoped>
.card {
  height: 100px;
}
</style>
