<template>
  <nav class="navbar container" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <a class="navbar-item" href="/">
        <strong class="is-size-4">Draw&Guess</strong>
      </a>
      <a
          role="button"
          class="navbar-burger burger"
          aria-label="menu"
          aria-expanded="false"
          data-target="navbarBasicExample"
      >
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>
    <div id="navbar" class="navbar-menu">
      <div class="navbar-start">
        <router-link to="/" class="navbar-item">Lobby</router-link>
        <router-link v-if="isLogin" to="/personal" class="navbar-item">Personal</router-link>
      </div>
      <div class="navbar-end">
        <div class="navbar-item">
          <div class="buttons">
            <button class="button is-light" @click.left="openLoginDialog($event)" v-if="!isLogin">
              Log in
            </button>
            <button class="button is-light" @click.left="logout($event)" v-else>
              Log out
            </button>
          </div>
        </div>
      </div>
    </div>
    <div v-if="clickLogin">
      <div class="modal is-active">
        <div class="modal-background"></div>
        <div class="modal-card" style="max-width: 300px">
          <header class="modal-card-head">
            <p class="modal-card-title">Welcome</p>
            <button class="delete" aria-label="close" @click.left="clickLogin=false"></button>
          </header>
          <section class="modal-card-body">
            <input class="input is-info mb-1" v-model="username" placeholder="Username Here">
            <input class="input is-info" type="password" v-model="password" placeholder="Password Here">
          </section>
          <footer class="modal-card-foot" style="justify-content: flex-end;">
            <button class="button is-primary" @click.left="register($event)">
              <strong>Sign up</strong>
            </button>
            <button class="button is-light" @click.left="login($event)">
              Log in
            </button>
          </footer>
        </div>
      </div>
    </div>
  </nav>
</template>
<script>
import AccountService from "@/services/AccountService";

export default {
  name: 'Nav',
  data() {
    return {
      clickLogin: false,
      isLogin: false,
      username: "",
      password: "",
    }
  },
  async created() {
    let isLogin = await AccountService.isLogin();
    if (isLogin.err !== 0 || isLogin.data === false) {
      this.isLogin = false;
      this.clickLogin = true;
      if (this.$route.path !== "/") {
        this.$router.push({
          path: "/",
          name: 'lobby',
          params: {},
        });
      }
    } else {
      this.isLogin = true;
      this.clickLogin = false;
    }
  },
  methods: {
    openLoginDialog(e) {
      this.clickLogin = true;
      e.preventDefault();
    },
    async register(e) {
      let result = await AccountService.register(this.username, this.password);
      if (result.err === 0) {
        await this.login(e);
      } else {
        alert(result.errMsg);
      }
      e.preventDefault();
    },
    async login(e) {
      let result = await AccountService.login(this.username, this.password)
      if (result.err === 0) {
        let data = result.data;
        this.isLogin = true;
        this.$cookies.set("userId", data.userId);
        this.$cookies.set("username", data.username);
        this.$cookies.set("csrfToken", data.csrfToken);
        this.clickLogin = false;
        this.$emit("updateRoomListRequest");
      } else {
        alert(result.errMsg)
      }
      e.preventDefault();
    },
    logout(e) {
      AccountService.logout();
      this.isLogin = false;
      this.$cookies.remove("userId");
      this.$cookies.remove("username");
      this.$emit("updateRoomListRequest");
      if (this.$route.path !== "/") {
        this.$router.push({
          path: "/",
          name: 'lobby',
          params: {},
        });
        e.preventDefault();
      }
    }
  },
};
</script>
<style lang="scss" scoped>
nav {
  margin-top: 25px;
  margin-bottom: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #d88d00;
    }
  }
}
</style>
