<template>
  <div class="rooms container">
    <h2 class="subtitle is-3">Lobby</h2>
    <div class="columns is-multiline is-vcentered">
      <div class="column is-one-quarter">
        <button class="card button is-size-2 has-text-warning-dark"
                @click.left="openCreateRoomDialog($event)">
          <strong>+</strong>
        </button>
      </div>
      <div
          v-for="room in roomList"
          :room="room"
          :key="room.id"
          class="column is-one-quarter "
      >
        <RoomCard :room="room"/>
      </div>
    </div>
    <div v-if="clickCreate">
      <div class="modal is-active">
        <div class="modal-background"></div>
        <div class="modal-card" style="max-width: 400px">
          <header class="modal-card-head">
            <p class="modal-card-title">Create Room</p>
            <button class="delete" aria-label="close" @click.left="clickCreate=false"></button>
          </header>
          <section class="modal-card-body">
            <input class="input is-info mb-1" v-model="roomName" placeholder="Room Name Here">
            <input class="input is-info" type="password" v-model="roomPassword"
                   placeholder="Password Here(Empty for no password)">
          </section>
          <footer class="modal-card-foot" style="justify-content: flex-end;">
            <button class="button is-primary" @click.left="createRoom($event)">
              <strong>Create</strong>
            </button>
          </footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RoomCard from "@/components/RoomCard";
import RoomService from "@/services/RoomService";

export default {
  name: "RoomList",
  components: {
    RoomCard,
  },
  data() {
    return {
      room: {},
      roomList: [],
      clickCreate: false,
      roomName: "",
      roomPassword: "",
      roomDesc: "",
    };
  },
  created() {
    this.getRoomList();
  },
  // NEW
  methods: {
    async getRoomList() {
      RoomService.getRoomList()
          .then(
              (result => {
                if (result.err === 0) {
                  let data = result.data;
                  this.$set(this, "roomList", data);
                } else {
                  alert(result.errMsg)
                  this.$set(this, "roomList", []);
                }
              }).bind(this)
          );
    },
    openCreateRoomDialog(e) {
      this.clickCreate = true;
      e.preventDefault();
    },
    async createRoom(e) {
      await RoomService.createRoom(this.roomName, this.roomPassword)
          .then(
              (result => {
                if (result.err === 0) {
                  let data = result.data;
                  this.clickCreate = false;
                  this.$router.push({
                    name: 'roomSingle',
                    params: {
                      id: data,
                    },
                  });
                } else {
                  alert(result.errMsg)
                }
              })
          );
      e.preventDefault();
    },
    updateRoomList() {
      this.getRoomList();
    },
  }
}
</script>

<style scoped>
.rooms {
  margin-top: 50px;
  text-align: center;
}
</style>
